import React, { useState } from 'react';
import styles from './PropBetListItem.module.css';

interface Props {
    id : number;
    expanded : boolean;
}

const PropListBody: React.FC<Props> = (props) => {
    let cn;
    if (props.expanded) {
        cn = 'body.expanded';
    } else {
        cn = 'body'
    }
    return <div className={cn}></div>
};

export default PropListBody;